.popup__title {
    font-weight: 900;
    font-size: 24px;
    line-height: 29px;
    color: #000;
    align-self: flex-start;
    margin: 0 0 20px 0;
    padding: 0;
    text-align: left;
}

@media screen and (max-width: 768px) {
    .popup__title {
        font-size: 18px;
        line-height: 22px;
        margin: 0 0 45px 0;
    }
}