.profile__name {
    grid-area: profile__name;
    color: #FFF;
    font-weight: 500;
    font-size: 42px;
    line-height: 1,2;
    text-align: left;
    margin: 0 18px 0 0;
    padding: 0;
    text-overflow: ellipsis;
    white-space: nowrap;
    overflow: hidden;
    max-width: 294px;
}

@media screen and (max-width: 768px) {
    .profile__name {
        font-size: 27px;
        line-height: 100%;
        text-align: center;
        margin: 0;
        padding: 0 0 0 20px;
        max-width: 189px;
    }
}